@import url("https://fonts.googleapis.com/css2?family=Montserrat+Alternates:wght@400;600;700&display=swap");

:root {
  --header-height: 3rem;
  --first-color: #da2535;
  --first-color-alt: #c42130;
  --first-color-send: #de3b49;
  --title-color: #161212;
  --text-color: #5b5757;
  --text-color-light: #8f8a8a;
  --body-color: #fefbfb;
  --container-color: #fff;

  --body-font: "Montserrat Alternates", sans-serif;

  --biggest-font-size: 2rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --h4-font-size: 1rem;
  --normal-font-size: 0.938rem;
  --small-font-size: 0.813rem;

  @media screen and (min-width: 968px) {
    --biggest-font-size: 3rem;
    --h2-font-size: 1.75rem;
    --h3-font-size: 1.25rem;
    --h4-font-size: 1.125rem;
    --normal-font-size: 1rem;
    --small-font-size: 0.875rem;
  }

  /*========== Font weight ==========*/
  --font-semi-bold: 600;
  --font-bold: 700;

  /*========== Margenes ==========*/
  --mb-1: 0.5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;

  /*========== z index ==========*/
  --z-tooltip: 10;
  --z-fixed: 100;
}

*,
::before,
::after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body.dark-theme {
  --first-color-send: #161212;
  --title-color: #f3f1f1;
  --text-color: #d1c7c8;
  --body-color: #251d1e;
  --container-color: #302728;
}

.change-theme {
  position: absolute;
  right: 1.5rem;
  top: 2.2rem;
  display: flex;
  color: var(--title-color);
  font-size: 2rem;
  cursor: pointer;
}

body {
  margin: var(--header-height) 0 0 0;
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  background-color: var(--body-color);
  color: var(--text-color);
  line-height: 1.6;
}
h1,
h2,
h3,
h4,
ul,
p {
  margin: 0;
}

h1,
h2,
h3,
h4 {
  font-weight: var(--font-semi-bold);
  color: var(--title-color);
}

ul {
  padding: 0;
  list-style: none;
}
a {
  text-decoration: none;
}
button {
  border: none;
  outline: none;
  cursor: pointer;
}
img {
  max-width: 100%;
  height: auto;
}

/*========== CLASS CSS ==========*/
.section {
  padding: 4rem 0 2rem;

  &-title,
  &-title-center {
    font-size: var(--h2-font-size);
    color: var(--title-color);
    text-align: center;
    margin-bottom: var(--mb-3);
  }
}

/*========== LAYOUT ==========*/

.main {
  overflow: hidden;
}
.bd-grid {
  display: grid;
  gap: 1.5rem;
}
.bd-container {
  max-width: 968px;
  width: calc(100% - 3rem);
  margin-left: var(--mb-3);
  margin-right: var(--mb-3);
}

.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--body-color);
}

/*========== NAV ==========*/
.nav {
  height: var(--header-height);
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__menu {
    @media screen and (max-width: 768px) {
      position: fixed;
      top: -100%;
      left: 0;
      right: 0;
      width: 90%;
      margin: 0 auto;
      padding: 2.5rem 0 0;
      text-align: center;
      background-color: var(--body-color);
      transition: 0.4s;
      box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);
      border-radius: 2rem;
      z-index: var(--z-fixed);
    }
  }
  &__item {
    margin-bottom: var(--mb-3);
  }
  &__link,
  &__logo,
  &__toggle {
    color: var(--title-color);
    font-weight: var(--font-semi-bold);
  }

  &__logo {
    &:hover {
      color: var(--first-color);
    }
  }
  &__link {
    transition: 0.3s;

    &:hover {
      color: var(--first-color);
    }
  }

  &__toggle {
    font-size: 1.3rem;
    cursor: pointer;
  }
}

/* Show menu */
.show-menu {
  top: calc(var(--header-height) + 1rem);
}

/* Active menu link */
.active-link {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    bottom: -0.75rem;
    left: 45%;
    width: 5px;
    height: 5px;
    background-color: var(--title-color);
    border-radius: 50%;
  }
}

/* Change background header */
.scroll-header {
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.1);
}

.show-scroll {
  visibility: visible;
  bottom: 1.5rem;
}

/*========== HOME ==========*/
.home {
  &__container {
    row-gap: 0.5rem;
  }
  &__img {
    width: 280px;
    justify-self: center;
  }
  &__title {
    font-size: var(--biggest-font-size);
    font-weight: var(--font-bold);
    margin-bottom: var(--mb-2);
  }
  &__description {
    margin-bottom: var(--mb-3);
  }
}

/*========== BUTTONS ==========*/
.button {
  display: inline-block;
  background-color: var(--first-color);
  color: #fff;
  padding: 1rem 1.5rem;
  border-radius: 0.5rem;
  font-weight: var(--font-semi-bold);
  transition: 0.3s;

  &:hover {
    background-color: var(--first-color-alt);
  }

  &-link {
    background: none;
    padding: 0;
    color: var(--first-color);

    &:hover {
      background-color: transparent;
      color: var(--first-color-alt);
    }
  }
}

/*========== ACCESSORIES ==========*/
.menu {
  &__container {
    grid-template-columns: repeat(2, 1fr);
    padding-bottom: 2rem;
  }

  &__content {
    position: relative;
    display: grid;
    padding: 0.25rem 0.75rem 0.75rem;
    background-color: var(--container-color);
    box-shadow: 0 2px 6px rgba(65, 11, 16, 0.15);
    border-radius: 1rem;
    align-items: center;
    &:hover {
      box-shadow: 0 3px 12px rgba(65, 11, 16, 0.15);
    }
  }
  &__img {
    width: 110px;
    justify-self: center;
    margin-bottom: 0.25rem;
  }

  &__title,
  &__category {
    text-align: center;
  }

  &__title {
    font-size: var(--normal-font-size);
  }

  &__category {
    font-size: var(--small-font-size);
    margin-bottom: var(--mb-1);
    color: var(--text-color-light);
  }

  &__price {
    font-weight: var(--font-semi-bold);
    color: var(--title-color);
  }

  &__button__cart {
    position: absolute;
    bottom: 0;
    right: 0;
    display: flex;
    font-size: 1.25rem;
    padding: 0.5rem 0.625rem;
    border-radius: 1rem 0 1rem 0;
  }
}

/*========== FOOTER ==========*/
.footer {
  &__logo {
    color: var(--title-color);
  }
  &__title {
    margin-bottom: var(--mb-2);
  }

  &__logo,
  &__title {
    font-size: var(--h3-font-size);
  }

  &__social {
    font-size: 1.5rem;
    color: var(--title-color);
    margin-right: var(--mb-3);

    &:hover {
      color: var(--first-color);
    }
  }

  &__copy {
    text-align: center;
    font-size: var(--small-font-size);
    color: var(--text-color-light);
    margin-top: 4rem;
  }
}

/*========== CART ==========*/
.cart {
  transition: 0.5s;
  max-height: 3em;
  position: fixed;
  bottom: -100%;
  overflow: hidden;
  padding: 0.5rem 1rem 1rem;
  border-radius: 1.5rem;
  background-color: var(--container-color);
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.1);
  z-index: var(--z-fixed);
  display: grid;
  gap: 0.5rem;
  overflow-y: scroll;
  &__container {
    color: var(--title-color);
    display: grid;
    gap: 0.5rem;
    grid-template-rows: 2rem max(20rem) 8rem;
    align-content: space-between;
    font-weight: var(--font-semi-bold);
  }

  &__button {
    font-size: 1.3rem;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &__body {
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    border-radius: 1.5rem;
    background-color: var(--body-color);
    padding: 0.5rem;
    overflow-y: scroll;
  }

  &__item {
    display: grid;
    grid-template-columns: 1fr 1.6rem 1.7rem 1.6rem;
    padding: 0.5rem;
    &__button {
      display: inline-flex;
      font-size: 1.25rem;
      padding: 0.2rem 0.2rem;
      border-radius: 1rem;
    }

    &__count {
      margin: 0 0.5rem;
    }
  }

  &__footer {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-between;
    &__trash {
      font-size: 1rem;
      padding: 0.5rem 1rem;
      border-radius: 1rem;
    }
    &__order {
      font-size: 1rem;
      padding: 0.5rem 1rem;
      border-radius: 1rem;
    }
  }

  &__form {
    &__wrapper {
      display: grid;
      gap: 0.2rem;
      border-radius: 1rem;
      background-color: var(--body-color);
      padding: 0.3rem;
      .input {
        border: none;
        outline: none;
        color: var(--text-color);
        background-color: inherit;
        font-size: var(--h4-font-size);
        font-family: var(--body-font);
        padding: 0.5rem;
        &::placeholder {
          font-size: var(--h4-font-size);
          font-family: var(--body-font);
        }
      }
    }
  }
}

.preview {
  max-height: 3em;
  bottom: 2%;
  left: 3%;
  right: 3%;
}

.show {
  max-height: 95%;
  transition: 0.5s;
}

/*========== MEDIA QUERIES ==========*/
@media screen and (max-width: 359px) {
  .home__img,
  .send__img {
    width: 100%;
  }
}

@media screen and (min-width: 576px) {
  .home__container,
  .send__container {
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }

  .home {
    &__container {
      padding: 5rem 0 0;
    }
    &__img {
      order: 1;
    }
  }

  .section-title-center {
    text-align: initial;
  }

  .home__img {
    width: 100%;
  }

  .cart {
    ::-webkit-scrollbar {
      width: 0;
    }
  }

  ::-webkit-scrollbar {
    width: 0;
  }
}

@media screen and (min-width: 768px) {
  body {
    margin: 0;
  }

  .section {
    padding-top: 7rem;
  }

  .nav {
    height: calc(var(--header-height) + 1.5rem);

    &__list {
      display: flex;
      align-items: center;
    }
    &__item {
      margin-left: var(--mb-5);
      margin-bottom: 0;
    }
    &__toggle {
      display: none;
    }
  }

  .change-theme {
    position: initial;
    margin-left: var(--mb-4);
  }

  .home__container {
    padding: 7rem 2rem 0;
  }

  .menu {
    &__container {
      grid-template-columns: repeat(4, 224px);
      justify-content: center;
    }
    &__content {
      padding: 0.5rem 1.5rem 0.75rem;
    }
    &__img {
      width: 120px;
      margin-bottom: var(--mb-1);
    }
    &__title,
    &__category {
      text-align: initial;
    }
    &__button {
      padding: 0.75rem;
    }
  }

  .cart {
    left: auto;
    width: 30%;
  }
}

@media screen and (min-width: 968px) {
  .bd-container {
    margin-left: auto;
    margin-right: auto;
  }

  .home__img,
  .send__img {
    width: 469px;
  }

  .home__container,
  .send__container {
    column-gap: 5rem;
  }
}
